@use 'base/variables' as v;
@use 'base/mixins' as m;
@use "sass:color"; 

.perfil{

    &_contenedor{
        @include m.contenedor_sm;
    }

    &_tag{
        @include m.tag;
        &{
            display: flex;
            flex-direction: column;
        }
        &-nombre{
            text-align: center;
        }
        p{
            font-size: 2.4rem;
        }
    }
}